.ux-offcanvas-menu-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.ux-offcanvas-menu-top a {
  line-height: rem-calc(24);
}

.ux-offcanvas-menu-content {
  flex: auto;
  position: relative;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

.ux-offcanvas-menu ul {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
}

.ux-offcanvas-menu ul ul {
  display: none;
  left: 100%;
}

.ux-offcanvas-menu ul .active-trail > ul {
  display: block;
}

.ux-offcanvas-menu a {
  display: block;
}

.ux-offcanvas-menu-trail a {
  display: inline-block;
}

.ux-offcanvas-menu-trail a::after {
  content: ':';
  display: inline-block;
}

.ux-offcanvas-menu-trail a:last-child {
  pointer-events: none;
}

.ux-offcanvas-menu-trail a:last-child::after {
  display: none;
}
